import React from 'react'
import { Button, Modal, Input, Icon, Upload, Checkbox } from 'antd'
import Common from '/page/common.jsx'
const ButtonGroup = Button.Group
const CheckboxGroup = Checkbox.Group;

class Case extends Common{

	async componentWillMount () {
		this.search()
	}

	state = Object.assign({}, this.state, {
		search: Object.assign({}, this.state.pageconf),
		columns: [{
			title: '项目编号',
			dataIndex: 't1'
		},{
			title: '项目名称',
			dataIndex: 't2'
		},{
			title: '人员',
			dataIndex: 't3'
		},{
			title: '证书类型',
			dataIndex: 't4'
		},{
			title: '证书简介',
			dataIndex: 't5'
		}],
		modalConf: {visible: false, item: {}},
		picmodelConf: {visible: false, item: {}},
		plainOptions: ['编码', '姓名', '身份证号', '社保号','学历证书', '学位证书', '家庭住址', '出生日期', '手机号', '技能', '特长']
	})

	search = async _ => {
		let data = [{key: 1, t1: 'BDG8763', t2: '移动运维三期', t3: '李明', t4: '计算机等级证书', t5: '计算机二级'},
		{key: 2, t1: 'BDG8763', t2: '移动运维三期', t3: '李明', t4: '计算机等级证书', t5: '计算机二级'},
		{key: 3, t1: 'BDG8763', t2: '移动运维三期', t3: '李明', t4: '计算机等级证书', t5: '计算机二级'},
		{key: 4, t1: 'BDG8763', t2: '移动运维三期', t3: '李明', t4: '计算机等级证书', t5: '计算机二级'},
		{key: 5, t1: 'BDG8763', t2: '移动运维三期', t3: '李明', t4: '计算机等级证书', t5: '计算机二级'},
		{key: 6, t1: 'BDG8763', t2: '移动运维三期', t3: '李明', t4: '计算机等级证书', t5: '计算机二级'},
		{key: 7, t1: 'BDG8763', t2: '移动运维三期', t3: '李明', t4: '计算机等级证书', t5: '计算机二级'},
		{key: 8, t1: 'BDG8763', t2: '移动运维三期', t3: '李明', t4: '计算机等级证书', t5: '计算机二级'}]
		this.setState({
				tabledata: data, 
				loading: false,
				pagination: Object.assign({}, this.state.pagination, {
					total: 8,
					current: 0
				})
			})
	}

	delete = _ => {
		this.handleOk(deletesupplier, 'id', '删除')
	}

	done = async _ => {
		let modalConf = {visible: false, item: {}}
		await this.setState({ modalConf })
		this.research()
	}

	openPicModel = _ => {
		this.setState({picmodelConf: {visible: true}})
	}

	excelImport = _ => {
		this.setState({visible: true})
	}

	renderSearch = _ => <div>
		<Input 
		value={this.state.search.name}
		suffix={this.state.search.name ? <Icon 
		type="close-circle" 
		onClick={_ => this.changeSearch({name: ''})} /> : null}
		onChange={e => this.changeSearch({name: e.target.value})}
		style={{width: 250}}
		placeholder="输入人员名称" />
		<ButtonGroup>
		<Button 
		onClick={this.search}
		type="primary" icon="search">搜索</Button>
		<Button 
		onClick={this.research}
		type="primary" icon="retweet">重置</Button>
		<Button 
		onClick={_ => this.addmodal('modalConf', '添加')}
		type="primary" icon="plus">添加</Button>
		<Button 
		onClick={_ => this.editmodal('modalConf', '编辑')}
		type="primary" icon="edit">修改</Button>
		<Button 
		onClick={this.changemodel}
		type="primary" icon="delete">删除</Button>
		<Button 
		onClick={this.openPicModel}
		type="primary" icon="picture">影像</Button>
		<Button 
		onClick={this.excelImport}
		type="primary" icon="file-excel">简历导出</Button>
		</ButtonGroup>
	</div>

	rendermodal = _ => [
		<Modal title="模版简历导出"
		  visible={this.state.visible}
		  onOk={this.delete}
		  mask={true}
		  width={400}
		  onCancel={this.changemodel}
		  okText="导出"
		  cancelText="取消"
		>
			<CheckboxGroup options={this.state.plainOptions} />
		</Modal>]

}

export default Case


